<template src="./BillSpliter.html"></template>
<style lang="scss" src="./BillSpliter.scss"></style>

<script>
const DEBUG_FLAG = true;
const TOTAL_LITERAL = "TOTAL";

function debug(msg) {
	if (DEBUG_FLAG) {
		console.log(msg);
	}
}

function getTransferScheme(netPayableMap) {
	debug(`getTransferScheme called with ${JSON.stringify(netPayableMap)}`);
	// construct & sort net payable arr
	let netPayableArr = [];
	for (let person in netPayableMap) {
		if (person != TOTAL_LITERAL) {
			netPayableArr.push({
				person: person,
				netPayable: netPayableMap[person]
			})
		}
	}
	netPayableArr.sort(function(x, y) {
		if (x.netPayable > y.netPayable) {
			return -1;
		}
		else if (x.netPayable < y.netPayable) {
			return 1;
		}
		return 0;
	});
	// construct transfer map
	let transferScheme = [];
	let l = 0;
	let r = netPayableArr.length - 1;
	while (l < r) {
		if (netPayableArr[l].netPayable === 0) {
			l += 1;
		}
		else if (netPayableArr[r].netPayable === 0) {
			r -= 1;
		}
		else {
			const from = netPayableArr[l].person;
			const to = netPayableArr[r].person;
			const amount = Math.min(netPayableArr[l].netPayable, -netPayableArr[r].netPayable);  
			transferScheme.push({ from, to, amount });
			netPayableArr[l].netPayable -= amount;
			netPayableArr[r].netPayable += amount;
		}
	}
	return transferScheme;
}

export default {
	name: 'BillSpliter',
	data: function() {
		return {
			input: {
				fields: [
					{
						key: 'action',
						label: '',
					},
					{
						key: 'name',
						label: 'Item Name',
						sortable: true,
					}, 
					{
						key: 'cost',
						label: 'Cost',
						sortable: true,
					}, 
					{
						key: 'taxed',
						label: 'Taxed',
					}, 
					{
						key: 'claim',
						label: 'Share Claim',
					}, 
					{
						key: 'paidBy',
						label: 'Paid By',
					},
				],
				items: [],
				taxRate: 0.0,
				enablePaidByMode: false,
				maxNameLen: 30,
			},

			result: {
				fields: [
					{
						key: 'personId',
						label: 'Person',
						sortable: false,
					},
					{
						key: 'payable',
						label: 'Payable',
						sortable: false,
					},
					{
						key: 'action',
						label: '',
						sortable: false,
					},
				],
				summary:  [],
				taxAdaptedCost: [],
				totalClaimShare: [],
				costPerClaimShare: [],
				totalPaidShare: [],
				costPerPaidShare: [],

				calculated: false,
				consistent: true,
				dirty: false,

				payableMap: {},
				paidMap: {},
				transferScheme: [],

				payableSum: 0,
				paidSum: 0,
				costSum: 0,

				precision: 2,
				consistentTol: 1e-2,

				showTransferResult: false,
				showPayableResult: false,
			},

			alert: {
				main: {
					message: '',
					variant: '',
					countDown: 0,
					initCountDown: 2,
				},
				sidebar: {
					message: '',
					variant: '',
					countDown: 0,
					initCountDown: 2,
				},
				modalShare: {
					message: '',
					variant: '',
					countDown: 0,
					initCountDown: 2,
				},
			},

			people: {
				maxNameLen: 20,
				toAdd: '',
				maxCount: 10,
				// for some reason v-model updates immediately for people that already exist. Thus I must manage people this way
				idList: ['0', '1'],
				idIdle: ['2', '3', '4', '5', '6', '7', '8', '9',],
				details: {
					'0': { name: 'GM', initial: 'GM', label: '0', personClass: 'person-class-0', claim: [], paid: [], },
					//'0': { name: '', initial: '', label: '0', personClass: 'person-class-0', claim: [], paid: [], },
					'1': { name: 'HF', initial: 'HF', label: '1', personClass: 'person-class-1', claim: [], paid: [], },
					//'1': { name: '', initial: '', label: '1', personClass: 'person-class-1', claim: [], paid: [], },
					'2': { name: '', initial: '', label: '2', personClass: 'person-class-2', claim: [], paid: [], },
					'3': { name: '', initial: '', label: '3', personClass: 'person-class-3', claim: [], paid: [], },
					'4': { name: '', initial: '', label: '4', personClass: 'person-class-4', claim: [], paid: [], },
					'5': { name: '', initial: '', label: '5', personClass: 'person-class-5', claim: [], paid: [], },
					'6': { name: '', initial: '', label: '6', personClass: 'person-class-6', claim: [], paid: [], },
					'7': { name: '', initial: '', label: '7', personClass: 'person-class-7', claim: [], paid: [], },
					'8': { name: '', initial: '', label: '8', personClass: 'person-class-8', claim: [], paid: [], },
					'9': { name: '', initial: '', label: '9', personClass: 'person-class-9', claim: [], paid: [], },
				},
			},

			modal: {
				share: {
					show: false,
					itemIndex: null,
					mode: null,
				},
				details: {
					show: false
				}
			}
		}
	},
	computed: {
		modalShareTitle: function() {
			debug(`modalShareTitle called with share.itemIndex = ${this.modal.share.itemIndex}`);
			if (this.modal.share.show && Number.isInteger(this.modal.share.itemIndex)) {
				return `${this.input.items[this.modal.share.itemIndex].name} -- ${this.modal.share.mode}`;
			}
			else {
				return `TITLE -- ${this.modal.share.mode}`;
			}
		},
		detailsVariant: function() {
			return this.result.consistent ? 'success' : 'danger';
		},
	},
	methods: {
		onCalculateHandler: function() {
			debug(`onCalculateHandler called`);
			// validate input
			if (!this.validateInput()) {
				return;
			}
			// setup
			this.clearResults();
			// loop items to get payable/paid
			for (let personId of this.people.idList) {
				this.result.payableMap[personId] = [];
				this.result.paidMap[personId] = [];
			}
			for (let itemIndex in this.input.items) {
				const item = this.input.items[itemIndex];
				this.result.taxAdaptedCost[itemIndex] = item.taxed ? item.cost * (1.0 + this.input.taxRate / 100.0) : item.cost;
				// payable
				this.result.totalClaimShare.push(this.getItemTotalClaimShare(itemIndex));
				this.result.costPerClaimShare[itemIndex] = this.result.taxAdaptedCost[itemIndex] / this.result.totalClaimShare[itemIndex];
				for (let personId of this.people.idList) {
					const share = this.getItemClaimShare(personId, itemIndex);
					this.result.payableMap[personId].push({
						share: share,
						cost: share * this.result.costPerClaimShare[itemIndex],
					});
				}
				// paid
				if (this.input.enablePaidByMode) {
					this.result.totalPaidShare.push(this.getItemTotalPaidShare(itemIndex));
					this.result.costPerPaidShare[itemIndex] = this.result.taxAdaptedCost[itemIndex] / this.result.totalPaidShare[itemIndex];
					for (let personId of this.people.idList) {
						const share = this.getItemPaidShare(personId, itemIndex);
						this.result.paidMap[personId].push({
							share: share,
							cost: share * this.result.costPerPaidShare[itemIndex],
						});
					}
				}
				this.result.costSum += this.result.taxAdaptedCost[itemIndex];
			}
			// construct result summary
			this.result.payableSum = 0.0;
			this.result.paidSum = 0.0;
			for (let personId of this.people.idList) {
				let isRelavant = false;
				let info = { personId };
				info.payable = this.result.payableMap[personId].reduce((currentVal, x) => {
					if (x.share != 0) {
						isRelavant = true;
					}
					return currentVal + x.cost;
				}, 0);
				this.result.payableSum += info.payable;
				if (this.input.enablePaidByMode) {
					info.paid = this.result.paidMap[personId].reduce((currentVal, x) => {
						if (x.share != 0) {
							isRelavant = true;
						}
						return currentVal + x.cost;
					}, 0);
					info.netPayable = info.payable - info.paid;
					this.result.paidSum += info.paid;
				}
				if (isRelavant) {
					this.result.summary.push(info);
				}
			}
			// construct transfer scheme
			if (this.input.enablePaidByMode) {
				let netPayableMap = {};
				for (let info of this.result.summary) {
					netPayableMap[info.personId] = info.netPayable;
				}
				this.result.transferScheme = getTransferScheme(netPayableMap);
			}
			// set status, check consistency
			this.result.calculated = true;
			this.result.dirty = false;
			this.result.consistent = Math.abs(this.result.payableSum - this.result.costSum) < this.result.consistentTol;
			debug(`>> ${(this.result.payableSum)} >>> ${(this.result.costSum)}`);
			if (this.result.consistent) {
				this.showAlert(this.alert.main, 'Calculation done', 'success');
				this.result.showTransferResult = this.input.enablePaidByMode;
				this.result.showPayableResult = !this.input.enablePaidByMode;
			}
			else {
				this.showAlert(this.alert.main, 'Inconsisitent results', 'warning');
			}
		},
		validateInput: function() {
			debug(`validateInput called`);
			// check tax rate
			if (this.input.taxRate < 0) {
				this.showAlert(this.alert.main, 'Invalid tax rate', 'danger');
				return false;
			}
			// check items 
			if (this.input.items.length === 0) {
				this.showAlert(this.alert.main, 'No item, no calculation needed', 'warning');
				return false;
			}
			for (const itemIndex in this.input.items) {
				if (this.getItemTotalClaimShare(itemIndex) <= 0) {
					this.showAlert(this.alert.main, `Someone must claim ${this.input.items[itemIndex].name}`, 'danger');
					return false;
				}
				if (this.input.enablePaidByMode && this.getItemTotalPaidShare(itemIndex) <= 0) {
					this.showAlert(this.alert.main, `Someone must pay for ${this.input.items[itemIndex].name}`, 'danger');
					return false;
				}
			}
			return true;
		},
		clearResults: function() {
			debug(`clearResults called`);
			this.result.summary = [];
			this.result.taxAdaptedCost = [];
			this.result.totalClaimShare = [];
			this.result.costPerClaimShare = [];
			this.result.totalPaidShare = [];
			this.result.costPerPaidShare = [];

			this.result.calculated = false;
			this.result.consistent = true;
			this.result.dirty = true;

			this.result.payableMap = {};
			this.result.paidMap = {};
			this.result.transferScheme = [];

			this.result.payableSum = 0;
			this.result.paidSum = 0;
			this.result.costSum = 0;
		},
		onAddItemHandler: function() {
			debug(`onAddItemHandler called`);
			const name = `Item ${this.input.items.length + 1}`;
			const cost = 0.0;
			const taxed = false;
			this.input.items.push(
				{ name, cost, taxed, }
			);
			Object.values(this.people.details).forEach(value => {
				value.claim.push(0);
				value.paid.push(0);
			});
			this.result.dirty = true;
		},
		onDeleteItemHandler: function(index) {
			debug(`onDeleteItemHandler called with ${index}`);
			Object.values(this.people.details).forEach(person => {
				delete person.claim.splice(index, 1);
				delete person.paid.splice(index, 1);
			});
			this.input.items.splice(index, 1);
			this.result.dirty = true;
		},
		onResetItemHandler: function(index) {
			debug(`onResetItemHandler called with ${index}`);
			this.input.items[index].name = '';
			this.input.items[index].cost = 0;
			this.input.items[index].taxed = false;
		},
		showAlert: function(alertObj, message, variant) {
			alertObj.message = message;
			alertObj.variant = variant;
			alertObj.countDown = alertObj.initCountDown;
		},
		onAlertMainCountDownChanged: function(countDown) {
			this.alert.main.countDown = countDown;
		},
		onAlertSideBarCountDownChanged: function(countDown) {
			this.alert.sidebar.countDown = countDown;
		},
		onAlertModalShareCountDownChanged: function(countDown) {
			this.alert.modalShare.countDown = countDown;
		},
		onAddPersonHandler: function() {
			debug(`onAddPersonHandler called`);
			this.people.toAdd = this.people.toAdd.trim();
			if (this.people.toAdd) {
				if (this.people.idList.length === this.people.maxCount) {
					this.showAlert(this.alert.sidebar, "Max number reached", "warning");
				}
				else {
					debug(`adding ${this.people.toAdd}`);
					// construct id, name, initial, label and personClass for the new person
					let name = this.people.toAdd;
					let initial = name[0].toUpperCase();
					if (name.includes(' ')) {
						let index = name.length - 1;
						while (name[index - 1] !== ' ') {
							index -= 1;
						}
						initial += name[index];
					}
					const personId = this.people.idIdle[0];
					this.people.idIdle.shift();
					this.people.idList.push(personId);
					this.people.details[personId].name = name;
					this.people.details[personId].initial = initial;
					this.people.details[personId].claim = new Array(this.input.items.length);
					this.people.details[personId].paid = new Array(this.input.items.length);
					this.people.details[personId].claim.fill(0);
					this.people.details[personId].paid.fill(0);
				}
				this.people.toAdd = ''; 
			}
		},
		onDeletePersonHandler: function(index) {
			debug(`onDeletePersonHandler called with ${index}`);
			const personId = this.people.idList[index];
			this.people.idList.splice(index, 1);
			this.people.idIdle.push(personId);
			if (this.people.details[personId].claim.some((x) => x !== 0)) {
				this.result.dirty = true;
				this.result.showTransferResult = false;
				this.result.showPayableResult = false;
			}
			if (this.people.details[personId].paid.some((x) => x !== 0)) {
				this.result.dirty = true;
				this.result.showTransferResult = false;
				this.result.showPayableResult = false;
			}
		},
		onOpenModalShareHandler: function(row, mode) {
			debug(`onOpenModalShareHandler called with ${JSON.stringify(row)}`);
			this.modal.share.show = true;
			this.modal.share.itemIndex = row.index;
			this.modal.share.mode = mode;
		},
		onCloseModalShareHandler: function() {
			debug(`onCloseModalShareHandler called`);
			this.modal.share.show = false;
			this.modal.share.itemIndex = undefined;
			this.modal.share.mode = undefined;
			this.result.dirty=true;
		},
		onToggleEnablePaidByModeHandler: function() {
			debug(`onToggleEnablePaidByModeHandler called`);
			if (!this.input.enablePaidByMode) {
				// off => on
				this.result.dirty = true;
				this.result.showTransferResult = true;
				this.result.showPayableResult = false;

				this.result.fields.splice(1, 0, 
					{
						key: 'netPayable',
						label: 'Net Payable',
						sortable: false,
					}
				);
				this.result.fields.splice(3, 0, 
					{
						key: 'paid',
						label: 'Paid',
						sortable: false,
					}
				);
			}
			else {
				// on => off
				this.result.showTransferResult = false;
				this.result.showPayableResult = true;
				this.result.fields.splice(3, 1);
				this.result.fields.splice(1, 1);
			}
		},
		onDetailHandler: function() {
			debug(`onDetailHandler called`);
			this.modal.details.show = true;
		},
		getItemClaimShare: function(personId, itemIndex, mode=Number) {
			const share = this.people.details[personId].claim[itemIndex];
			if (mode === String) {
				return share > 0 ? String(share) : false;
			}
			else {
				return share;
			}
		},
		getItemPaidShare: function(personId, itemIndex, mode=Number) {
			const share = this.people.details[personId].paid[itemIndex];
			if (mode === String) {
				return share > 0 ? String(share) : false;
			}
			else {
				return share;
			}
		},
		getItemTotalClaimShare: function(itemIndex) {
			return this.people.idList.reduce((currentVal, personId) => {
				return currentVal + this.people.details[personId].claim[itemIndex]
			}, 0);
		},
		getItemTotalPaidShare: function(itemIndex) {
			return this.people.idList.reduce((currentVal, personId) => {
				return currentVal + this.people.details[personId].paid[itemIndex]
			}, 0);
		},
		getDetailsStr: function(personId, itemIndex) {
			let rst = '';
			const item = this.input.items[itemIndex];
			const totClaim = this.getItemTotalClaimShare(itemIndex);
			const claim = this.getItemClaimShare(personId, itemIndex);
			const totPaid = this.getItemTotalPaidShare(itemIndex);
			const paid = this.getItemPaidShare(personId, itemIndex);
			const taxed = this.input.items[itemIndex].taxed;
			const itemCost = this.input.items[itemIndex].cost * (taxed ? (1.0 + this.input.taxRate / 100.0) : 1.0);
			const claimcost = claim / totClaim * itemCost;
			const paidcost = paid / totPaid * itemCost;
			if (claim > 0) {
				rst += `${item.name} ($${itemCost.toFixed(this.result.precision)}): claimed ${claim}/${totClaim} -- $ ${claimcost.toFixed(this.result.precision)}`;
			}
			else {
				rst += `${item.name} ($${itemCost.toFixed(this.result.precision)}): not claimed`;
			}
			if (this.input.enablePaidByMode) {
				if (paid > 0) {
					rst += `, paid ${paid}/${totPaid} -- $ ${paidcost.toFixed(this.result.precision)}`;
				}
				else {
					rst += `, not paid`;
				}
			}
			return rst + '.';
		},
		onExportCsvHandler: function() {
			debug(`onExportCsvHandler called`);
		},
		onExportXlsxHandler: function() {
			debug(`onExportXlsxHandler called`);
		},
		onImportFileHandler: function() {
			debug(`onImportFileHandler called`);
		},
		modalShareFormatter: function(value) {
			if (!value) {
				return 0;
			}
			const num = Number(value);
			return Math.max(num, 0);
		},
		sidebarPeopleFormatter: function(value) {
			if (value.length > this.people.maxNameLen) {
				return value.slice(0, value.length - 1);
			}
			return value;
		},
		itemNameFormatter: function(value) {
			if (value.length > this.input.maxNameLen) {
				return value.slice(0, value.length - 1);
			}
			return value;
		},
		itemCostFormatter: function(value) {
			return (!value) ? 0 : Number(value);
		},
		taxRateFormatter: function(value) {
			if (!value) {
				return 0;
			}
			const num = Number(value);
			return Math.min(Math.max(num, 0), 100);
		},
	},
	components: {
	},
};
</script>
